___CSS_LOADER_EXPORT___.push([module.id, `#about-container {
  flex-grow: 1;
  align-self: center;
  padding: 0 0;
  margin-block: 2.5vh;
  width: clamp(300px, 95vw, 1000px);
  display: grid;
  grid-template-columns: auto;
  justify-content: center;
  align-items: center;
  grid-row-gap: 2rem;
  line-height: 1.6;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-color: rgba(42, 42, 42, 0.9);
  border-radius: 10px;
}

.section:nth-child(even) {
  flex-direction: row-reverse;
}

.img {
  aspect-ratio: 1;
  width: clamp(300px, 65vw, 400px);
  border-radius: 10px;
}

.text-container {
  /* aspect-ratio: 4/3; */
  width: clamp(300px, 60vw, 450px);
  padding: 1rem min(5rem, 3vw);
  text-align: center;
}
.text-container h3,
.text-container p {
  font-family: "Open Sans", sans-serif;
}
.text-container h3 {
  color: #ebd693;
  text-transform: uppercase;
  margin-bottom: 2rem;
  letter-spacing: 3px;
}
.text-container p {
  font-size: calc(12px + 0.390625vw);
  color: white;
}

@media only screen and (max-width: 800px) {
  .section,
  .section:nth-child(even) {
    flex-direction: column;
    min-width: 95vw;
    gap: 1rem;
  }
