  font-family: "Poppins", sans-serif;
}

#homepage-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0.8fr, 1fr));
  place-items: center;
  gap: 1rem;
  margin-block: 1rem;
}
#homepage-container > div {
  width: clamp(300px, 95vw, 1000px);
  /* width: min(1200px, 100vw); */
  min-height: 400px;
  border-radius: 10px;
}

#welcome {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-inline: 0.8rem;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-size: cover;
  /* font-family: 'Merriweather', serif; */
}
#welcome #welcome-title {
  font-size: calc(28px + 0.390625vw);
  font-weight: 700;
  text-transform: uppercase;
  color: #ebd693;
}
#welcome #welcome-msg {
  margin-top: 2rem;
  font-size: calc(16px + 0.390625vw);
  max-width: 80%;
  color: white;
}

#hours {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 1rem;
  background-color: #2a2a2a;
  /* border-top: 1px solid \$gold-color; */
}
#hours #hours-msg {
  padding: 10%;
  flex: 1;
  font-size: calc(16px + 0.390625vw);
  color: #ebd693;
}
#hours #hours-div {
  flex: 1;
}
#hours #hours-info {
  max-width: 80%;
  padding: 2rem;
  border: 1px solid #4fa9cc;
  border-radius: 10px;
  background-color: #2a2a2a;
}
#hours #hours-info #title {
  font-size: calc(18px + 0.390625vw);
  padding: 0 1rem;
  border: 2px solid #4fa9cc;
  border-radius: 10px;
  color: white;
}
#hours #hours-info .heading {
  font-size: calc(16px + 0.390625vw);
  border-bottom: 1px solid #4fa9cc;
}
#hours #hours-info .days {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: white;
}
#hours #hours-info .times {
  font-size: calc(14px + 0.390625vw);
  color: white;
}

@media only screen and (max-width: 800px) {
  #hours {
    flex-direction: column;
  }
  #hours #hours-info {
    max-width: 100%;
    padding: 2.5rem;
  }
