  position: relative;
  min-height: 500px !important;
}
.slider-container .slide {
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
}
.slider-container .slide.active-slide {
  display: flex;
}
.slider-container .slide > div {
  display: flex;
  flex-direction: column-reverse;
  gap: 20px;
  color: rgb(226, 219, 219);
  background-color: rgba(44, 42, 42, 0.5647058824);
  max-width: min(80%, 500px);
  min-height: 50%;
  border-radius: 20px;
  padding: 20px;
  font-size: calc(24px + 0.33vw);
  text-align: center;
}
.slider-container .slide > div i {
  font-size: calc(24px + 0.8vw);
}
.slider-container .slide > div span {
  color: #4fa9cc;
  font-size: calc(24px + 0.6vw);
}
.slider-container > button {
  position: absolute;
  top: 90%;
  width: 60px;
  height: 50px;
  background-color: transparent;
  border: none;
  opacity: 60%;
  cursor: pointer;
}
.slider-container > button i {
  font-family: "Font Awesome 6 Free";
  color: #fff;
  font-size: calc(24px + 1vw);
}
.slider-container .prev-btn {
  left: 1%;
}
.slider-container .next-btn {
  right: 1%;
}
.slider-container .dot {
  position: absolute;
  bottom: 4%;
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 10px;
  opacity: 30%;
  cursor: pointer;
}
.slider-container .dot.active-dot {
  opacity: 100%;
}
.slider-container #dot0 {
  left: 42.5%;
}
.slider-container #dot1 {
  left: 47.5%;
}
.slider-container #dot2 {
  left: 52.5%;
}
.slider-container #dot3 {
  left: 57.5%;
}

