}

#slide3 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

#slide0,
#slide1,
#slide2,
#slide3 {
  background-size: cover;
  background-position: center;
}

.flex-centered {
  display: flex;
  justify-content: center;
  align-items: center;
